.before-after-container{
  margin: 20% auto;
  text-align: center;
  border: 10px solid orange;
  justify-items: center;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 90vh;
  }

h1 {
 display: block;
 margin-bottom: 40px;
}

.image-container {
display: block;
 max-height: 50%;
 position: relative;
 margin-bottom: 10%;
border: 7px solid orange;
}

.image-container img {
 display: block;
 position: relative;
 border: 7px solid lime;
 /* max-width: 90%; */
 margin: auto;
 max-height: 50vh;
}

.img-slider-btn {
 all: unset;
 display: block;
 position: absolute;
 top: 0;
 bottom: 0;
 padding: 3rem;
 cursor: pointer;
 transition: background-color 100ms ease-in-out;
}


.img-slider-btn > * {
  stroke: white;
  fill: black;
  min-width: 5rem;
  min-height: 5rem;
}

.overlay {
 position: absolute;
 top: 0;
 width: 100%;
 min-height: 50vh;
 max-height: 50vh;
 border: 7px solid lime;
 background-color: rgba(0, 0, 0, 0);
 /* Initially transparent */
 transition: background-color 0.3s ease;
 /* Smooth transition */
}

.overlay:hover{
 /* background-color: rgba(0, 0, 0, 0.5); */
 /* Opaque overlay on hover */
 background-image: url('../images/After/Supreme\ F350/driver_exterior.jpg');
 background-size: contain;
 background-position: center;
 /* margin: auto; */

 max-height: 50vh;
 background-repeat: no-repeat;
}
